<template><section><h2 id="dai-li-mo-shi"><a class="header-anchor" href="#dai-li-mo-shi" aria-hidden="true">¶</a> 代理模式</h2>
<p>代理模式是为一个对象提供一个代用ֵ或站位符，以便控制对它的访问。</p>
<p>代理模式的关键是，当客户不方便直接访问一个对象或者不满足需求的时候，提供一个替身对象来控制对这个对象的访问，客户实际上访问的是替身对象。</p>
<h3 id="xu-ni-dai-li"><a class="header-anchor" href="#xu-ni-dai-li" aria-hidden="true">¶</a> 虚拟代理</h3>
<p>如果网速够快，我们就不需要预加载，那就不需要代理对象，可以选择直接请求本体。</p>
<p>下列使用虚拟代理实现，图片占位符预加载。</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div id=\&quot;box\&quot;&gt;\n  &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data() {\n    return {\n\n    }\n  },\n  methods: {\n    proxImage() {\n      let myImage = (() =&gt; {\n        let imgNode = document.createElement('img')\n        document.getElementById('box').appendChild(imgNode)\n        return function(src) {\n          imgNode.src = src\n        }\n      })()\n\n      let proxyImage = (() =&gt; {\n        let img = new Image()\n        img.onload = function() {\n          myImage(this.src)\n        }\n        return function(src) {\n          myImage('http://www.w3school.com.cn/i/eg_chinarose.jpg')\n          img.src = src\n        }\n      })()\n\n      proxyImage('http://www.w3school.com.cn/i/eg_tulip.jpg')\n    }\n  },\n  mounted() {\n    this.proxImage()\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <div id="box">
  </div>
</template>


</div>
                            <p>使用虚拟代理实现图片预加载</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"box"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {

    }
  },
  <span class="hljs-attr">methods</span>: {
    proxImage() {
      <span class="hljs-keyword">let</span> myImage = <span class="hljs-function">(<span class="hljs-params">(</span>) =&gt;</span> {
        <span class="hljs-keyword">let</span> imgNode = <span class="hljs-built_in">document</span>.createElement(<span class="hljs-string">'img'</span>)
        <span class="hljs-built_in">document</span>.getElementById(<span class="hljs-string">'box'</span>).appendChild(imgNode)
        <span class="hljs-keyword">return</span> <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">src</span>) </span>{
          imgNode.src = src
        }
      })()

      <span class="hljs-keyword">let</span> proxyImage = <span class="hljs-function">(<span class="hljs-params">(</span>) =&gt;</span> {
        <span class="hljs-keyword">let</span> img = <span class="hljs-keyword">new</span> Image()
        img.onload = <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
          myImage(<span class="hljs-keyword">this</span>.src)
        }
        <span class="hljs-keyword">return</span> <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">src</span>) </span>{
          myImage(<span class="hljs-string">'http://www.w3school.com.cn/i/eg_chinarose.jpg'</span>)
          img.src = src
        }
      })()

      proxyImage(<span class="hljs-string">'http://www.w3school.com.cn/i/eg_tulip.jpg'</span>)
    }
  },
  mounted() {
    <span class="hljs-keyword">this</span>.proxImage()
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3 id="xu-ni-dai-li-he-bing-http-qing-qiu"><a class="header-anchor" href="#xu-ni-dai-li-he-bing-http-qing-qiu" aria-hidden="true">¶</a> 虚拟代理合并HTTP请求</h3>
<p>在web开发中，最大的开销就是网络请求。</p>
<p>假设我们在做一个文件同步的功能，当我们选中一个 checkbox 的时候，它对应的文件就会被同步到另外一台备用服务器上面</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div&gt;\n    &lt;el-checkbox label=\&quot;文件1\&quot; @change=\&quot;uploadFile\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox label=\&quot;文件2\&quot; @change=\&quot;uploadFile\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox label=\&quot;文件3\&quot; @change=\&quot;uploadFile\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox label=\&quot;文件4\&quot; @change=\&quot;uploadFile\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox label=\&quot;文件5\&quot; @change=\&quot;uploadFile\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox label=\&quot;文件6\&quot; @change=\&quot;uploadFile\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;el-checkbox label=\&quot;文件7\&quot; @change=\&quot;uploadFile\&quot;&gt;&lt;/el-checkbox&gt;\n    &lt;div v-for=\&quot;(item,index) in showList\&quot; style=\&quot;margin-top:30px\&quot;&gt;{{item}}&lt;/div&gt;\n    \n&lt;/div&gt;&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      showList:[]\n    }\n  },\n  methods: {\n        uploadFile () {\n      let target = event.target\n      let synchronousFile = (id)=&gt; {\n        this.showList.push(`${id}开始同步文件.....`)\n      }\n\n      let proxySynchronousFile = (function () {\n        let cache = [], // 保存一段时间内需要同步的ID\n            timer // 定时器\n        return function (id) {\n          cache.push(id)\n          if (timer) { // 保证不会覆盖已经启动的定时器\n            return\n          }\n          timer = setTimeout(function () {\n            synchronousFile(cache.join(',')) // 2 秒后向本体发送需要同步的ID 集合\n            clearTimeout(timer) // 清空定时器\n            timer = null\n            cache.length = 0 // 清空ID 集合\n          }, 2000)\n        }\n      })()\n      if (target.checked) {\n        proxySynchronousFile(target.value)\n      }\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <div>
    <el-checkbox label="文件1" @change="uploadFile"></el-checkbox>
    <el-checkbox label="文件2" @change="uploadFile"></el-checkbox>
    <el-checkbox label="文件3" @change="uploadFile"></el-checkbox>
    <el-checkbox label="文件4" @change="uploadFile"></el-checkbox>
    <el-checkbox label="文件5" @change="uploadFile"></el-checkbox>
    <el-checkbox label="文件6" @change="uploadFile"></el-checkbox>
    <el-checkbox label="文件7" @change="uploadFile"></el-checkbox>
    <div v-for="(item,index) in showList" style="margin-top:30px">{{item}}</div>
    
</div></template>


</div>
                            <p>虚拟代理合并HTTP请求</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"文件1"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"uploadFile"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"文件2"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"uploadFile"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"文件3"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"uploadFile"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"文件4"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"uploadFile"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"文件5"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"uploadFile"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"文件6"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"uploadFile"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"文件7"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"uploadFile"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">el-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(item,index) in showList"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-top:30px"</span>&gt;</span>{{item}}<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">showList</span>:[]
    }
  },
  <span class="hljs-attr">methods</span>: {
        uploadFile () {
      <span class="hljs-keyword">let</span> target = event.target
      <span class="hljs-keyword">let</span> synchronousFile = <span class="hljs-function">(<span class="hljs-params">id</span>)=&gt;</span> {
        <span class="hljs-keyword">this</span>.showList.push(<span class="hljs-string">`<span class="hljs-subst">${id}</span>开始同步文件.....`</span>)
      }

      <span class="hljs-keyword">let</span> proxySynchronousFile = (<span class="hljs-function"><span class="hljs-keyword">function</span> (<span class="hljs-params"></span>) </span>{
        <span class="hljs-keyword">let</span> cache = [], <span class="hljs-comment">// 保存一段时间内需要同步的ID</span>
            timer <span class="hljs-comment">// 定时器</span>
        <span class="hljs-keyword">return</span> <span class="hljs-function"><span class="hljs-keyword">function</span> (<span class="hljs-params">id</span>) </span>{
          cache.push(id)
          <span class="hljs-keyword">if</span> (timer) { <span class="hljs-comment">// 保证不会覆盖已经启动的定时器</span>
            <span class="hljs-keyword">return</span>
          }
          timer = setTimeout(<span class="hljs-function"><span class="hljs-keyword">function</span> (<span class="hljs-params"></span>) </span>{
            synchronousFile(cache.join(<span class="hljs-string">','</span>)) <span class="hljs-comment">// 2 秒后向本体发送需要同步的ID 集合</span>
            clearTimeout(timer) <span class="hljs-comment">// 清空定时器</span>
            timer = <span class="hljs-literal">null</span>
            cache.length = <span class="hljs-number">0</span> <span class="hljs-comment">// 清空ID 集合</span>
          }, <span class="hljs-number">2000</span>)
        }
      })()
      <span class="hljs-keyword">if</span> (target.checked) {
        proxySynchronousFile(target.value)
      }
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>

</section></template>

<script>
export default {
  data () {
    return {
      showList:[]
    }
  },
  methods: {
    proxImage () {
      let myImage = (() => {
        let imgNode = document.createElement('img')
        document.getElementById('box').appendChild(imgNode)
        return function (src) {
          imgNode.src = src
        }
      })()

      let proxyImage = (() => {
        let img = new Image()
        img.onload = function () {
          myImage(this.src)
        }
        return function (src) {
          myImage('http://www.w3school.com.cn/i/eg_chinarose.jpg')
          img.src = src
        }
      })()

      proxyImage('http://www.w3school.com.cn/i/eg_tulip.jpg')
    },
    uploadFile () {
      let target = event.target
      let synchronousFile = (id)=> {
        this.showList.push(`${id}开始同步文件.....`)
      }

      let proxySynchronousFile = (function () {
        let cache = [], // 保存一段时间内需要同步的ID
            timer // 定时器
        return function (id) {
          cache.push(id)
          if (timer) { // 保证不会覆盖已经启动的定时器
            return
          }
          timer = setTimeout(function () {
            synchronousFile(cache.join(',')) // 2 秒后向本体发送需要同步的ID 集合
            clearTimeout(timer) // 清空定时器
            timer = null
            cache.length = 0 // 清空ID 集合
          }, 2000)
        }
      })()
      if (target.checked) {
        proxySynchronousFile(target.value)
      }
    }
  },
  mounted () {
    this.proxImage()
  }
}
</script>


