<template><section><h2 id="ce-lue-mo-shi"><a class="header-anchor" href="#ce-lue-mo-shi" aria-hidden="true">¶</a> 策略模式</h2>
<p>策略模式的定义是:定义一系列的算法，把它们各自封装成策略类，算法被 封装在策略类内部的方法里。</p>
<p>将不变的部分和变化的部分隔开是每个设计模式的主题，策 略模式也不例外，策略模式的目的就是将算法的使用与算法的实现分离开来。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<p>策略模式也可以用来封装 一系列的“业务规则”。只要这些业务规则指向的目标一致，并且可以被替换使用，我们就可以 用策略模式来封装它们。</p>
<p>如下，表单校验
<br></p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div&gt;\n    &lt;el-form :label-position=\&quot;labelPosition\&quot; label-width=\&quot;80px\&quot; :model=\&quot;formLabelAlign\&quot;&gt;\n      &lt;div style=\&quot;width:400px\&quot;&gt;\n        &lt;el-form-item label=\&quot;用户名\&quot;&gt;\n          &lt;el-input v-model=\&quot;formLabelAlign.name\&quot; @blur=\&quot;name\&quot;&gt;&lt;/el-input&gt;\n          &lt;span class=\&quot;error\&quot;&gt;{{errorMsg.name}}&lt;/span&gt;\n        &lt;/el-form-item&gt;\n        &lt;el-form-item label=\&quot;密码\&quot;&gt;\n          &lt;el-input v-model=\&quot;formLabelAlign.password\&quot; @blur=\&quot;password\&quot;&gt;&lt;/el-input&gt;\n          &lt;span class=\&quot;error\&quot;&gt;{{errorMsg.password}}&lt;/span&gt;\n        &lt;/el-form-item&gt;\n        &lt;el-form-item label=\&quot;手机号\&quot;&gt;\n          &lt;el-input v-model=\&quot;formLabelAlign.mobile\&quot; @blur=\&quot;mobile\&quot;&gt;&lt;/el-input&gt;\n          &lt;span class=\&quot;error\&quot;&gt;{{errorMsg.mobile}}&lt;/span&gt;\n        &lt;/el-form-item&gt;\n      &lt;/div&gt;\n    &lt;/el-form&gt;\n  &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n/** *********************策略对象**************************/\nconst strategies = {\n  isNonEmpty: function(value, errorMsg) {\n    if (value === '') {\n      return errorMsg\n    }\n  },\n  minLength: function(value, length, errorMsg) {\n    if (value.length &lt; length) {\n      return errorMsg\n    }\n  },\n  isMobile: function(value, errorMsg) {\n    if (!/(^1[3|5|8][0-9]{9}$)/.test(value)) {\n      return errorMsg\n    }\n  }\n}\n/***********************Validator 类**************************/\nclass Validator {\n  constructor() {\n    this.cache = []\n  }\n  add(value, rules) {\n    let _this = this\n    rules.forEach((rule) =&gt; {\n      let strategyAry = rule.strategy.split(':')\n      let errorMsg = rule.errorMsg\n      _this.cache.push(function() {\n        let strategy = strategyAry.shift()\n        strategyAry.unshift(value)\n        strategyAry.push(errorMsg)\n        return strategies[strategy].apply(this, strategyAry)\n      })\n    })\n  }\n  start() {\n    for (var i = 0, validatorFunc; validatorFunc = this.cache[i++];) {\n      var errorMsg = validatorFunc()\n      if (errorMsg) {\n        return errorMsg\n      }\n    }\n  }\n}\n\nexport default {\n  data() {\n    return {\n      labelPosition: 'right',\n      formLabelAlign: {\n        name: '',\n        password: '',\n        mobile: ''\n      },\n      errorMsg: {\n        name: '',\n        password: '',\n        mobile: ''\n      }\n    }\n  },\n  methods: {\n    name() {\n      this.blur('name', [{\n        strategy: 'isNonEmpty',\n        errorMsg: '用户名不能为空'\n      }, {\n        strategy: 'minLength:6',\n        errorMsg: '用户名长度不能小于6 位'\n      }])\n    },\n    password() {\n      this.blur('password', [{\n        strategy: 'isNonEmpty',\n        errorMsg: '密码不能为空'\n      }, {\n        strategy: 'minLength:6',\n        errorMsg: '密码长度不能小于6 位'\n      }])\n    },\n    mobile() {\n      this.blur('mobile', [{\n        strategy: 'isNonEmpty',\n        errorMsg: '手机号不能为空'\n      }, {\n        strategy: 'isMobile',\n        errorMsg: '请输入合法手机号'\n      }])\n    },\n    blur(obj, errorMsg) {\n      let validator = new Validator()\n      validator.add(this.formLabelAlign[obj], errorMsg);\n      this.errorMsg[obj] = validator.start()\n    }\n  },\n  mounted() {\n\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.btn {\n  display: inline-block;\n  line-height: 1;\n  white-space: nowrap;\n  cursor: pointer;\n  background: #fff;\n  border: 1px solid #c4c4c4;\n  color: #1f2d3d;\n  margin: 0;\n  padding: 10px 15px;\n  border-radius: 4px;\n}\n\n.error {\n  color: #f00;\n}\n&quot;}">
                            <div class="source" slot="source"><template>
  <div>
    <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
      <div style="width:400px">
        <el-form-item label="用户名">
          <el-input v-model="formLabelAlign.name" @blur="name"></el-input>
          <span class="error">{{errorMsg.name}}</span>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="formLabelAlign.password" @blur="password"></el-input>
          <span class="error">{{errorMsg.password}}</span>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="formLabelAlign.mobile" @blur="mobile"></el-input>
          <span class="error">{{errorMsg.mobile}}</span>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>


</div>
                            <p>不使用Element校验机制的情况下，使用策略模式来完成校验。</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">el-form</span> <span class="hljs-attr">:label-position</span>=<span class="hljs-string">"labelPosition"</span> <span class="hljs-attr">label-width</span>=<span class="hljs-string">"80px"</span> <span class="hljs-attr">:model</span>=<span class="hljs-string">"formLabelAlign"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:400px"</span>&gt;</span>
