          <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"error"</span>&gt;</span>{{errorMsg.password}}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
