          <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"error"</span>&gt;</span>{{errorMsg.mobile}}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">el-form-item</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">el-form</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-comment">/** *********************策略对象**************************/</span>
<span class="hljs-keyword">const</span> strategies = {
  <span class="hljs-attr">isNonEmpty</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">value, errorMsg</span>) </span>{
    <span class="hljs-keyword">if</span> (value === <span class="hljs-string">''</span>) {
      <span class="hljs-keyword">return</span> errorMsg
    }
  },
  <span class="hljs-attr">minLength</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">value, length, errorMsg</span>) </span>{
    <span class="hljs-keyword">if</span> (value.length &lt; length) {
      <span class="hljs-keyword">return</span> errorMsg
    }
  },
  <span class="hljs-attr">isMobile</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">value, errorMsg</span>) </span>{
    <span class="hljs-keyword">if</span> (!<span class="hljs-regexp">/(^1[3|5|8][0-9]{9}$)/</span>.test(value)) {
      <span class="hljs-keyword">return</span> errorMsg
    }
  }
}
<span class="hljs-comment">/***********************Validator 类**************************/</span>
<span class="hljs-class"><span class="hljs-keyword">class</span> <span class="hljs-title">Validator</span> </span>{
  <span class="hljs-keyword">constructor</span>() {
    <span class="hljs-keyword">this</span>.cache = []
  }
  add(value, rules) {
    <span class="hljs-keyword">let</span> _this = <span class="hljs-keyword">this</span>
    rules.forEach(<span class="hljs-function">(<span class="hljs-params">rule</span>) =&gt;</span> {
      <span class="hljs-keyword">let</span> strategyAry = rule.strategy.split(<span class="hljs-string">':'</span>)
      <span class="hljs-keyword">let</span> errorMsg = rule.errorMsg
      _this.cache.push(<span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
        <span class="hljs-keyword">let</span> strategy = strategyAry.shift()
        strategyAry.unshift(value)
        strategyAry.push(errorMsg)
        <span class="hljs-keyword">return</span> strategies[strategy].apply(<span class="hljs-keyword">this</span>, strategyAry)
      })
    })
  }
  start() {
    <span class="hljs-keyword">for</span> (<span class="hljs-keyword">var</span> i = <span class="hljs-number">0</span>, validatorFunc; validatorFunc = <span class="hljs-keyword">this</span>.cache[i++];) {
      <span class="hljs-keyword">var</span> errorMsg = validatorFunc()
      <span class="hljs-keyword">if</span> (errorMsg) {
        <span class="hljs-keyword">return</span> errorMsg
      }
    }
  }
}

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">labelPosition</span>: <span class="hljs-string">'right'</span>,
      <span class="hljs-attr">formLabelAlign</span>: {
        <span class="hljs-attr">name</span>: <span class="hljs-string">''</span>,
        <span class="hljs-attr">password</span>: <span class="hljs-string">''</span>,
        <span class="hljs-attr">mobile</span>: <span class="hljs-string">''</span>
      },
      <span class="hljs-attr">errorMsg</span>: {
        <span class="hljs-attr">name</span>: <span class="hljs-string">''</span>,
        <span class="hljs-attr">password</span>: <span class="hljs-string">''</span>,
        <span class="hljs-attr">mobile</span>: <span class="hljs-string">''</span>
      }
    }
  },
  <span class="hljs-attr">methods</span>: {
    name() {
      <span class="hljs-keyword">this</span>.blur(<span class="hljs-string">'name'</span>, [{
        <span class="hljs-attr">strategy</span>: <span class="hljs-string">'isNonEmpty'</span>,
        <span class="hljs-attr">errorMsg</span>: <span class="hljs-string">'用户名不能为空'</span>
      }, {
        <span class="hljs-attr">strategy</span>: <span class="hljs-string">'minLength:6'</span>,
        <span class="hljs-attr">errorMsg</span>: <span class="hljs-string">'用户名长度不能小于6 位'</span>
      }])
    },
    password() {
      <span class="hljs-keyword">this</span>.blur(<span class="hljs-string">'password'</span>, [{
        <span class="hljs-attr">strategy</span>: <span class="hljs-string">'isNonEmpty'</span>,
        <span class="hljs-attr">errorMsg</span>: <span class="hljs-string">'密码不能为空'</span>
      }, {
        <span class="hljs-attr">strategy</span>: <span class="hljs-string">'minLength:6'</span>,
        <span class="hljs-attr">errorMsg</span>: <span class="hljs-string">'密码长度不能小于6 位'</span>
      }])
    },
    mobile() {
      <span class="hljs-keyword">this</span>.blur(<span class="hljs-string">'mobile'</span>, [{
        <span class="hljs-attr">strategy</span>: <span class="hljs-string">'isNonEmpty'</span>,
        <span class="hljs-attr">errorMsg</span>: <span class="hljs-string">'手机号不能为空'</span>
      }, {
        <span class="hljs-attr">strategy</span>: <span class="hljs-string">'isMobile'</span>,
        <span class="hljs-attr">errorMsg</span>: <span class="hljs-string">'请输入合法手机号'</span>
      }])
    },
    blur(obj, errorMsg) {
      <span class="hljs-keyword">let</span> validator = <span class="hljs-keyword">new</span> Validator()
      validator.add(<span class="hljs-keyword">this</span>.formLabelAlign[obj], errorMsg);
      <span class="hljs-keyword">this</span>.errorMsg[obj] = validator.start()
    }
  },
  mounted() {

  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.btn</span> {
  <span class="hljs-attribute">display</span>: inline-block;
