<template><section><h2 id="dan-li-mo-shi"><a class="header-anchor" href="#dan-li-mo-shi" aria-hidden="true">¶</a> 单例模式</h2>
<p>单例模式的定义是：保证一个类仅有一个实例，并提供一个访问它的全局访问点。应用场景不限于创建单个实例。</p>
<p>一般情况，推荐字面量的形式创建单个对象，而不是基于New的创建方式。设计模式仅提供一种思路，并不限于场景。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<p>在某些特殊情况，需要直接对DOM元素添加事件监听。</p>
<p>如下，由于每次事件执行都会添加绑定，结果导致元素绑定重复事件，增大性能开销。</p>
<br>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div&gt;\n    &lt;div&gt;\n      &lt;h4&gt;1、未使用单例&lt;/h4&gt;\n      &lt;el-button type=\&quot;primary\&quot; @click=\&quot;bind\&quot;&gt;\n        执行绑定 {{bindNum}}\n      &lt;/el-button&gt;\n      &lt;span ref=\&quot;btn\&quot; class=\&quot;btn\&quot;&gt;\n        被绑定元素\n      &lt;/span&gt;\n      事件处理次数:{{num}}\n    &lt;/div&gt;\n    &lt;hr&gt;\n    &lt;div&gt;\n      &lt;h4&gt;2、使用单例&lt;/h4&gt;\n      &lt;el-button type=\&quot;success\&quot; @click=\&quot;bind2\&quot;&gt;\n        执行绑定 {{bindNum2}}\n      &lt;/el-button&gt;\n      &lt;span ref=\&quot;btn2\&quot; class=\&quot;btn\&quot;&gt;\n        被绑定元素\n      &lt;/span&gt;\n      事件处理次数:{{num2}}\n    &lt;/div&gt;\n  &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data() {\n    return {\n      num: 0,\n      bindNum: 0,\n      num2: 0,\n      bindNum2: 0,\n      single: null\n    }\n  },\n  methods: {\n    toSingle(fn) {\n      let result\n      return result || (result = fn.apply(this, arguments))\n    },\n    bindEvent() {\n      this.$refs.btn.addEventListener('click', () =&gt; {\n        this.num++\n      })\n    },\n    bind() {\n      this.bindNum++\n      this.bindEvent();\n    },\n    bind2() {\n      this.bindNum2++\n      if (typeof this.single === 'function') {\n        this.single()\n      }\n    }\n  },\n  mounted() {\n    this.single = this.toSingle(() =&gt; {\n      this.$refs.btn2.addEventListener('click', () =&gt; {\n        this.num2++\n      })\n      return true\n    })\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.btn {\n  display: inline-block;\n  line-height: 1;\n  white-space: nowrap;\n  cursor: pointer;\n  background: #fff;\n  border: 1px solid #c4c4c4;\n  color: #1f2d3d;\n  margin: 0;\n  padding: 10px 15px;\n  border-radius: 4px;\n}\n&quot;}">
                            <div class="source" slot="source"><template>
  <div>
    <div>
      <h4>1、未使用单例</h4>
      <el-button type="primary" @click="bind">
        执行绑定 {{bindNum}}
      </el-button>
      <span ref="btn" class="btn">
        被绑定元素
      </span>
      事件处理次数:{{num}}
    </div>
    <hr>
    <div>
      <h4>2、使用单例</h4>
      <el-button type="success" @click="bind2">
        执行绑定 {{bindNum2}}
      </el-button>
      <span ref="btn2" class="btn">
        被绑定元素
      </span>
      事件处理次数:{{num2}}
    </div>
  </div>
</template>


</div>
                            <p>不使用单例模式的情况下绑定事件将导致元素被绑定多个重复事件。</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>1、未使用单例<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">el-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"bind"</span>&gt;</span>
        执行绑定 {{bindNum}}
      <span class="hljs-tag">&lt;/<span class="hljs-name">el-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"btn"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"btn"</span>&gt;</span>
        被绑定元素
      <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      事件处理次数:{{num}}
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">hr</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">h4</span>&gt;</span>2、使用单例<span class="hljs-tag">&lt;/<span class="hljs-name">h4</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">el-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"bind2"</span>&gt;</span>
        执行绑定 {{bindNum2}}
      <span class="hljs-tag">&lt;/<span class="hljs-name">el-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"btn2"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"btn"</span>&gt;</span>
        被绑定元素
      <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      事件处理次数:{{num2}}
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">num</span>: <span class="hljs-number">0</span>,
      <span class="hljs-attr">bindNum</span>: <span class="hljs-number">0</span>,
      <span class="hljs-attr">num2</span>: <span class="hljs-number">0</span>,
      <span class="hljs-attr">bindNum2</span>: <span class="hljs-number">0</span>,
      <span class="hljs-attr">single</span>: <span class="hljs-literal">null</span>
    }
  },
  <span class="hljs-attr">methods</span>: {
    toSingle(fn) {
      <span class="hljs-keyword">let</span> result
      <span class="hljs-keyword">return</span> result || (result = fn.apply(<span class="hljs-keyword">this</span>, <span class="hljs-built_in">arguments</span>))
    },
    bindEvent() {
      <span class="hljs-keyword">this</span>.$refs.btn.addEventListener(<span class="hljs-string">'click'</span>, () =&gt; {
        <span class="hljs-keyword">this</span>.num++
      })
    },
    bind() {
      <span class="hljs-keyword">this</span>.bindNum++
      <span class="hljs-keyword">this</span>.bindEvent();
    },
    bind2() {
      <span class="hljs-keyword">this</span>.bindNum2++
      <span class="hljs-keyword">if</span> (<span class="hljs-keyword">typeof</span> <span class="hljs-keyword">this</span>.single === <span class="hljs-string">'function'</span>) {
        <span class="hljs-keyword">this</span>.single()
      }
    }
  },
  mounted() {
    <span class="hljs-keyword">this</span>.single = <span class="hljs-keyword">this</span>.toSingle(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
      <span class="hljs-keyword">this</span>.$refs.btn2.addEventListener(<span class="hljs-string">'click'</span>, () =&gt; {
        <span class="hljs-keyword">this</span>.num2++
      })
      <span class="hljs-keyword">return</span> <span class="hljs-literal">true</span>
    })
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.btn</span> {
  <span class="hljs-attribute">display</span>: inline-block;
  <span class="hljs-attribute">line-height</span>: <span class="hljs-number">1</span>;
  <span class="hljs-attribute">white-space</span>: nowrap;
  <span class="hljs-attribute">cursor</span>: pointer;
  <span class="hljs-attribute">background</span>: <span class="hljs-number">#fff</span>;
  <span class="hljs-attribute">border</span>: <span class="hljs-number">1px</span> solid <span class="hljs-number">#c4c4c4</span>;
  <span class="hljs-attribute">color</span>: <span class="hljs-number">#1f2d3d</span>;
  <span class="hljs-attribute">margin</span>: <span class="hljs-number">0</span>;
  <span class="hljs-attribute">padding</span>: <span class="hljs-number">10px</span> <span class="hljs-number">15px</span>;
  <span class="hljs-attribute">border-radius</span>: <span class="hljs-number">4px</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<br>
<h3 id="todo"><a class="header-anchor" href="#todo" aria-hidden="true">¶</a> TODO</h3>
<p>在vue中 如<code v-pre="">@click</code>的事件，当一个 ViewModel 被销毁时，所有的事件处理器都会自动被删除。你无须担心如何自己清理它们。</p>
<p><strong><em>当我们需要自己绑定事件时就得避免事件重复绑定，以及及时移除不必要的事件。非特殊情况，推荐使用vue事件绑定</em></strong></p>


</section></template>
<style>
.btn {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #c4c4c4;
  color: #1f2d3d;
  margin: 0;
  padding: 10px 15px;
  border-radius: 4px;
}
</style>
<script>
export default {
  data() {
    return {
      num: 0,
      bindNum: 0,
      num2: 0,
      bindNum2: 0,
      single: null
    }
  },
  methods: {
    toSingle(fn) {
      let result
      return result || (result = fn.apply(this, arguments))
    },
    bindEvent() {
      this.$refs.btn.addEventListener('click', () => {
        this.num++
      })
    },
    bind() {
      this.bindNum++
      this.bindEvent();
    },
    bind2() {
      this.bindNum2++
      if (typeof this.single === 'function') {
        this.single()
      }
    }
  },
  mounted() {
    this.single = this.toSingle(() => {
      this.$refs.btn2.addEventListener('click', () => {
        this.num2++
      })
      return true
    })
  }
}
</script>


